export default {
	props: [
		'list', 'dotActiveColor','currentindex'
	],
	data() {
		return {
			type: 'center',
		}
	},
	created() {
		if (this.list.length > 5)
			this.type = 'flex-start'
		else this.type = 'center'
		// console.log(this.type)
	},
	methods: {
		click(index) {
			this.$emit('changeItem', index);
			// console.log(index)
		}
	},
	components: {

	}
}
